.alignright {
  float: right;
  margin: 0.25rem 0 1rem 1rem;
}

.alignleft {
  float: left;
  margin: 0.25rem 1rem 1rem 0;
}

a img.alignright {
  float: right;
  margin: 0.25rem 0 1rem 20px;
