:root {
  --max-width: 1000px;
  --min-margin: 1em;
}

html, body {
  color: #060607;
  container-type: inline-size;
  font: normal 125%/1.6 Nunito, system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji";
  height: 100%;
  margin: 0;
  padding: 0;
}

html.admin-bar {
  height: calc(100% - 32px);
}
@media only screen and (max-width: 782px) {
  html.admin-bar {
    height: calc(100% - 46px);
  }
}

a {
  outline-color: #6C5309;
  outline-offset: 0.2rem;
}
a:link, a:visited {
  color: #5D2189;
}
a:active, a:focus, a:hover {
  color: #6C5309;
}

body {
  display: grid;
  block-size: 100%;
  font-size: 0.9rem;
  grid-template-rows: auto 1fr auto;
  min-block-size: 100%;
}

main,
#site-header,
#site-footer {
  padding-inline: max(var(--min-margin), 50cqw - var(--max-width) / 2);
}

main .full-bleed {
  padding-inline: min(0px, var(--max-width) / 2 - 50cqw + var(--min-margin));
}

ul.menu {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

img {
  border: 1px solid #060607;
  border-radius: 0.25rem;
}

.hidden {
  display: none;
  visibility: hidden;
}

#site-header {
  background-color: #5D2189;
  margin-block-end: 1.2rem;
  padding-block-end: 1.2rem;
}
#site-header a {
  text-decoration: none;
}
#site-header #page-title {
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: bold;
  margin-block: 1.2rem 0;
}
#site-header .menu {
  display: flex;
  flex-direction: row;
  gap: 0.8rem;
}
#site-header .menu .menu-item {
  color: #FFFFFF;
  outline-color: #FFFFFF;
  outline-offset: 0.25rem;
  outline-width: 0.05rem;
}
#site-header .menu .menu-item:active, #site-header .menu .menu-item:focus, #site-header .menu .menu-item:hover {
  outline: solid;
}

.shelf {
  display: grid;
  grid-gap: 0.5rem;
  grid-template-columns: repeat(auto-fill, 150px);
  list-style-type: none;
  padding: 0;
}
.shelf .item {
  margin-inline: 0;
}
.shelf .item img {
  max-block-size: 220px;
  inline-size: 100%;
}

.articles {
  margin-block-start: 4rem;
}
#site-footer {
  background-color: #5D2189;
  color: #FFFFFF;
  display: grid;
  grid-gap: 0.5rem;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "listening reading" "greeting greeting" "copyright copyright";
  margin-block-start: 2.4rem;
}
#site-footer a {
  color: #FFFFFF;
  outline-color: #FFFFFF;
  outline-offset: 0.25rem;
  outline-width: 0.05rem;
}
#site-footer a:active, #site-footer a:focus, #site-footer a:hover {
  outline: solid;
}
#site-footer .reading,
#site-footer .listening {
  display: grid;
  grid-gap: 0;
  grid-template-columns: fit-content(300px) 1fr;
  grid-template-areas: "header header" "image text";
}
#site-footer .reading h3,
#site-footer .listening h3 {
  grid-area: header;
}
#site-footer .reading img,
#site-footer .listening img {
  block-size: 300px;
  max-inline-size: 300px;
  grid-area: image;
}
#site-footer .reading p,
#site-footer .listening p {
  align-self: end;
  padding-inline-start: 0.5rem;
  grid-area: text;
}
#site-footer .reading {
  grid-area: reading;
  margin-block-end: 1rem;
  position: relative;
}
#site-footer .reading figure {
  display: grid;
  grid-template-areas: "solo";
  align-content: center;
  left: 13.25rem;
  margin: 0;
  position: absolute;
  top: 5rem;
}
#site-footer .reading canvas {
  transform: rotate(-90deg);
}
#site-footer .reading canvas, #site-footer .reading figcaption {
  align-content: center;
  grid-area: solo;
  text-align: center;
}
#site-footer .listening {
  grid-area: listening;
  margin-block-end: 1rem;
}
#site-footer .listening img {
  inline-size: 300px;
  max-inline-size: auto;
}
#site-footer .greeting {
  grid-area: greeting;
  margin-block: 0;
}
#site-footer .copyright {
  grid-area: copyright;
  margin-block-start: 0;
}